:root {
  --ss-bg-color: rgba(32, 103, 209, .467);
  --ss-border-color: #bb9f11;
  --ss-primary-color: #ffd500;
  --ss-font-color: #fff;
}

body {
  color: #ebe7d5;
  background-color: #211;
  background-repeat: no-repeat;
  background-size: 100vw 100vh;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

.cat-info {
  align-items: center;
  justify-items: center;
  margin: 40px 0;
  display: grid;
}

.cat-image {
  height: 400px;
  border-radius: 5px;
  box-shadow: 0 0 50px 5px #1c6375;
}

.cat-name, .cat-description, .cat-temperament {
  max-width: 500px;
  text-shadow: 4px 4px 15px rgba(0, 0, 0, .6);
}

.cat-name, .cat-span, .success, .error {
  text-transform: uppercase;
  color: rgba(0, 199, 199, .96);
}

.loader-container {
  max-width: 350px;
  height: 50vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  animation: spin 4s linear infinite;
}

.success, .error {
  justify-content: center;
  display: flex;
  position: relative;
  top: -90px;
}

.breed-select {
  max-width: 400px;
  background-color: rgba(0, 0, 0, 0);
  border: 0 solid #0b5b86;
  justify-content: center;
  gap: 40px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  display: flex;
}

.is-hidden {
  display: none;
}

